import { useEffect, useState } from 'react';
import './App.css';
import Outdoor from './Outdoor';
import Service from './Service';
import Travel from './Travel';



function App() {
  var [data,setData] = useState({})
  useEffect(function () {
    fetch("./data.json")
    .then(function (response) {
      return response.json();
    })
    .then(function (result) {
      setData(result);
    })
  },[])

  console.log(data);

  return (
    <div className="App">
      <Travel Title="Surfing in Maldives" Text="It’s windy. The cool breeze of the ocean. It gives, a sense of beauty, in motion. All is flowing, rushing and tide-And I sit in wonder, dreaming beside."/>
      <Outdoor Title="Outdoor Experience"Text="It’s windy. The cool breeze of the ocean. It gives, a sense of beauty, in motion. All is flowing, rushing and tide-And I sit in wonder, dreaming beside."/>
      <Service Color={data.delivery && data.delivery.color} Icon="./On.svg" Title="On-time delivery" Text="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam aliquam diam sit"/>
      <Service Color={data.delivery && data.parcel.color} Icon="./Pa.svg" Title="Parcels" Text="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam aliquam diam sit"/>
      <Service Color={data.delivery && data.check.color} Icon="./Qu.svg" Title="Quality check" Text="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam aliquam diam sit"/>
      <Service Color={data.delivery && data.service.color} Icon="./Cu.svg" Title="Customer service" Text="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam aliquam diam sit"/>
    </div>
  );
}

export default App;
